.App {
  font-family: 'Arial', sans-serif;
  text-align: center;
  background-color: #f4f4f4;
  color: #333;
  margin: 0;
  padding: 0;
}

.App-header {
  background-color: #333;
  color: white;
  padding: 20px;
}

h1 {
  font-size: 2.5rem;
}

nav ul {
  list-style-type: none;
  padding: 0;
}

nav ul li {
  display: inline;
  margin: 0 15px;
}

nav ul li a {
  color: white;
  text-decoration: none;
  font-weight: bold;
}

nav ul li a:hover {
  color: #f0a500;
}

.section {
  padding: 40px 20px;
  margin: 20px 0;
}

h2 {
  font-size: 2rem;
  color: #444;
}

ul {
  list-style-type: square;
}

.project {
  margin-bottom: 20px;
}

footer {
  background-color: #333;
  color: white;
  padding: 10px;
}

footer p {
  font-size: 1rem;
}
